/* global window */
import React from 'react';
import sizeMe from 'react-sizeme';
import LazyLoad from 'react-lazyload';
import { compose, withProps } from 'recompose';
import { string, object, oneOfType } from 'prop-types';
import Grid from 'material-ui/Grid';
import Typography from 'material-ui/Typography';
import Paper from 'material-ui/Paper';
import { withStyles } from 'material-ui/styles';

import cloudinary from '../helpers/cloudinary.js';

const styles = theme => ({
  paper: {
    padding: theme.spacing.unit * 2,
    marginBottom: 0,
    position: 'relative',
    zIndex: 1,
    [theme.breakpoints.down('xs')]: {
      paddingBottom: theme.spacing.unit * 8,
    },
    [theme.breakpoints.up('sm')]: {
      marginBottom: theme.spacing.unit * 10,
    },
    [theme.breakpoints.up('md')]: {
      marginBottom: theme.spacing.unit * 20,
    },
    [theme.breakpoints.up('lg')]: {
      marginBottom: theme.spacing.unit * 30,
    },
  },

  display1: {
    marginBottom: theme.spacing.unit,
    [theme.breakpoints.down('xs')]: {
      fontSize: theme.typography.title.fontSize,
    },
  },

  headline: {
    [theme.breakpoints.down('xs')]: {
      fontSize: theme.typography.body2.fontSize,
    },
  },

  imgWrapper: {
    width: '100%',
    height: '100%',
    position: 'absolute',
    top: 0,
    left: 0,
  },

  img: {
    objectFit: 'cover',
    width: '100%',
    height: '100%',
  },

});

const Slide = ({ classes, title, body, image, className }) => (
  <div className={className}>
    <div className={classes.imgWrapper}>
      <LazyLoad
        height="100%"
        placeholder={<img src={image.src} className={classes.img} />}
        once
        offset={64}
      >
        <img {...image} className={classes.img} />
      </LazyLoad>
    </div>
    <Grid
      container
      className={className}
      alignItems="flex-end"
      justify="flex-end"
      spacing={0}
    >
      <Grid item xs={12} sm={12} md={6}>
        <Paper className={classes.paper}>
          <Typography className={classes.display1} type="display1">
            {title}
          </Typography>
          <Typography
            className={classes[!title ? 'display1' : 'headline']}
            type={!title ? 'display1' : 'headline'}
          >
            <span dangerouslySetInnerHTML={{ __html: body }} />
          </Typography>
        </Paper>
      </Grid>
    </Grid>
  </div>
);

Slide.propTypes = {
  classes: object,
  title: string,
  body: string,
  image: oneOfType([string, object]),
  className: string,
};

Slide.displayName = 'Slide';

export default compose(
  withStyles(styles),
  sizeMe({ monitorHeight: true }),
  withProps(({ image: publicId, size: { width, height } }) => ({
    image: cloudinary.getSrc({
      publicId,
      width: 1920,
    }, {
      cloudName: 'candt',
      width: width || window.innerWidth,
      height: height || window.innerHeight,
      crop: 'fill',
      gravity: 'auto',
    }),
  })),
)(Slide);



